.switch-wrp .switch {
  position: relative;
  display: inline-block;
  width: 36px;
  height: 18px;
  margin: 0 12px 0 0;
}
.switch-wrp .slider {
  position: absolute;
  cursor: pointer;
  top: 3px;
  left: 7px;
  right: -7px;
  bottom: -2px;
  background-color: #0074eb;
  transition: .4s;
  border-radius: 16px;
}
.switch-wrp .slider:before {
  position: absolute;
  content: "";
  width: 12px;
  height: 12px;
  left: -1px;
  bottom: 3px;
  background-color: #fff;
  transition: .4s;
  border-radius: 16px;
}

.switch-wrp .enabled:before {
  -webkit-transform: translateX(22px);
  transform: translateX(22px);
}
.switch-container
{
  display: flex;
  position: fixed;
  background: #212529;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  padding: 5px 20px;
  right: 6%;
  top: 0px;
}